<template>
  <header>
    <router-link to="/"><img alt="Vue logo" src="../assets/logo.png" height="120"></router-link>
    <nav>
      <ul>
        <li><router-link to="/">Home</router-link></li>
        <li><router-link to="/about">About us</router-link></li>
      </ul>
    </nav>
    <nav class="buttons">
      <ul class="ulbuttons">
        <li v-if="userLoggedIn" class="libuttons"><router-link to="/dashboard"><button class="button-log" role="button">Dashboard</button></router-link></li>
        <li v-if="!userLoggedIn" class="libuttons"><router-link to="/login"><button class="button-log" role="button">Login</button></router-link></li>
        <li v-if="!userLoggedIn" class="libuttons"><router-link to="/register"><button class="button-log" role="button">Register</button></router-link></li>
        <li v-if="userLoggedIn" v-on:click="disconect" class="liuser"><button class="button-log" role="button">Disconnect</button></li>
      </ul>
    </nav>
  </header>
</template>

<script>
  export default {
    name: 'app-header',
    data() {
      return {
        userLoggedIn: false,
      };
    },
    created() {
      if (localStorage.token) {
        this.userLoggedIn = true;
      }
      fetch("http://localhost:8080/ping")
        .then(res => {
          if (res.status === 200) {
            localStorage.server = "http://localhost:8080";
          } else {
            localStorage.server = "http://faster-area-api-prod.westeurope.azurecontainer.io:8080";
          }
        })
    },
    computed() {
      if (localStorage.token) {
        this.userLoggedIn = true;
      }
    },
    methods: {
      disconect() {
        localStorage.removeItem('token');
        localStorage.removeItem("action")
        localStorage.removeItem("reaction")
        localStorage.removeItem("nameact")
        localStorage.removeItem("namerea")
        localStorage.removeItem("path")
        this.userLoggedIn = false;
        this.$router.push({ name: 'Home' });
      },
    },
  }

</script>

